}
.navbar{
    opacity: 0.9;
    /* position: fixed;
    top:0; */
    width: 100%;
    height: auto;
   
   
}
 


nav  .nav-link{
    font-weight:500;  
    letter-spacing:  0.0375rem; 
    cursor: pointer;
}
nav ul li{
    text-transform: uppercase;
}
.navbar-nav>li>a{
    color:#fff!important;
    color:var(--primary-white)!important;
    margin-right: 0.75rem;
    margin-left: 00.75rem;
    border-top: 00.1875rem solid rgba(255,255,255,0);
    border-top: 00.1875rem solid var(--primary-dark);

}
.navbar-nav>li>a.active, .navbar-nav>li>a.active:focus{
    border-top: 0.1875rem solid #f9ab00;
    border-top: 0.1875rem solid var(--primary-orange);
}
.navbar-nav>li>a:hover{
    border-top: 0.1875rem solid crimson;

}
.home{
    display: flex;
    background: url(/Portfolio/static/media/mufee.ce3fc338.jpg) no-repeat center;
    width: 100%;
  height: auto;
   
    color: #fff;
    /* min-height: 500px; */
    background-size: cover;
    /* background-attachment: fixed; */
    font-family: 'Ubuntu', sans-serif;
}
.home .max-width{
  width: 100%;
  display: flex;
  
}
.home .max-width .row{
  margin-right: 0;
}
.home-content{
  margin-top: 20%;
  margin-left: 3%;
  padding-bottom: 2%;
 
}
.home .home-content .text-1{
    font-size: 27px;
}
.home .home-content .text-2{
    font-size: 75px;
    font-weight: 600;
    margin-left: -3px;
}
.home .home-content .text-3{
    font-size: 40px;
    margin: 5px 0;
}
.home .home-content .text-3 span{
    color: crimson;
    font-weight: 500;
}
.home .home-content a{
    display: inline-block;
    background: crimson;
    color: #fff;
    font-size: 25px;
    padding: 12px 36px;
    margin-top: 20px;
    font-weight: 400;
    border-radius: 6px;
    border: 2px solid crimson;
    transition: all 0.3s ease;
}
.home .home-content a:hover{
    color: crimson;
    background: none;
}

@media (max-width: 991px) {
    .max-width{
        padding: 0 50px;
    }
}
