        font-size: 50px;
    }
    .home .home-content .text-3{
        font-size: 32px;
    }
    .home .home-content a{
        font-size: 20px;
    }
}
@media (max-width: 500px) {
    .home .home-content .text-1{
        font-size: 19px;
    }
    .home .home-content .text-2{
        font-size: 26px;
    }
    .home .home-content .text-3{
        font-size: 27px;
    }
    .home{
        /* background-size: cover; */
        padding: 0 23px;
    }
    .home-content{
        margin-left:  0px;
        
    }
