  h1{
    display: flex;
     position: relative; 
    justify-content: center;
    align-items: center;
     padding-bottom:2% ;
     padding-top:8% ; 
     
   
}
  .skill-bars{
    padding: 25px 30px;
    /* width: 600px; */
    background: rgb(3, 3, 3);
    box-shadow: 5px 5px 20px rgba(0,0,0,0.2);
    /* border-radius: 10px; */
     max-width: 100%;
    height: 100vh; 
   
   
   
  }
  .skill-bars .bar{
    margin: 20px 0;
  }
  .skill-bars .bar:first-child{
    margin-top: 0px;
  }
  .skill-bars .bar .info{
    margin-bottom: 5px;
  }
  .skill-bars .bar .info span{
    font-weight: 500;
    font-size: 17px;
    opacity: 0; 
    color: wheat;
    -webkit-animation: showText 0.5s 1s linear forwards;
            animation: showText 0.5s 1s linear forwards;
  }
  @-webkit-keyframes showText {
    100%{
      opacity: 1;
    }
  }
  @keyframes showText {
    100%{
      opacity: 1;
    }
  }
  .skill-bars .bar .progress-line{
    height: 10px;
    width: 100%;
    background: #f0f0f0;
    position: relative;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    border-radius: 10px;
    box-shadow: inset 0 1px 1px rgba(0,0,0,0.05),
                0 1px rgba(255,255,255,0.8);
    -webkit-animation: animate 1s cubic-bezier(1,0,0.5,1) forwards;
            animation: animate 1s cubic-bezier(1,0,0.5,1) forwards;
  }
  @-webkit-keyframes animate {
    100%{
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }
  @keyframes animate {
    100%{
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }
  .bar .progress-line span{
    height: 100%;
    position: absolute;
    border-radius: 10px;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    background: #6665ee;
    -webkit-animation: animate 1s 1s cubic-bezier(1,0,0.5,1) forwards;
            animation: animate 1s 1s cubic-bezier(1,0,0.5,1) forwards;
  }
  .bar .progress-line.html span{
    width: 90%;
  }
  .bar .progress-line.css span{
    width: 60%;
  }
  .bar .progress-line.jquery span{
    width: 85%;
  }
  .bar .progress-line.python span{
    width: 50%;
  }
  .bar .progress-line.mysql span{
    width: 75%;
  }
  .progress-line span::before{
    position: absolute;
    content: "";
    top: -10px;
    right: 0;
    height: 0;
    width: 0;
    border: 7px solid transparent;
    border-bottom-width: 0px;
    border-right-width: 0px;
    border-top-color: #000;
    opacity: 0;
    -webkit-animation: showText2 0.5s 1.5s linear forwards;
            animation: showText2 0.5s 1.5s linear forwards;
  }
  .progress-line span::after{
    position: absolute;
    top: -28px;
    right: 0;
    font-weight: 500;
    background: #000;
    color: #fff;
    padding: 1px 8px;
    font-size: 12px;
    border-radius: 3px;
    opacity: 0;
    -webkit-animation: showText2 0.5s 1.5s linear forwards;
            animation: showText2 0.5s 1.5s linear forwards;
  }
  @-webkit-keyframes showText2 {
    100%{
      opacity: 1;
    }
  }
  @keyframes showText2 {
    100%{
      opacity: 1;
    }
  }
  .progress-line.html span::after{
    content: "90%";
  }
  .progress-line.css span::after{
    content: "60%";
  }
  .progress-line.jquery span::after{
    content: "85%";
  }
  .progress-line.python span::after{
    content: "50%";
  }
  .progress-line.mysql span::after{
    content: "75%";
  }
